  width: 100%;
  color: #fff;
  padding: 33px 0; }
  .main-header__wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center; }
  .main-header__menu {
    display: flex;
    flex-wrap: wrap;
    align-items: center; }
  .main-header__menu-link {
    display: flex;
    align-items: center;
    margin-left: 85px; }
  .main-header__menu-icon {
    margin-right: 15px; }
  .main-header__button {
    border: 1px solid #fff;
    color: #fff;
    margin-left: 50px; }

@media screen and (max-width: 1024px) {
  .main-header {
    padding: 20px 0; }
    .main-header__menu-link {
      font-size: 0;
      margin-left: 30px; }
    .main-header__menu-icon {
      margin-right: 0;
      width: 25px;
      height: 19px; }
    .main-header__menu-link:nth-of-type(2) .main-header__menu-icon {
      width: 25px;
      height: 25px; }
    .main-header__button {
      display: none; } }

.promo-block {
  height: 100vh;
