.popup {
  position: absolute;
  z-index: 11;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 490px;
  background: #00B2FF;
  padding: 35px 40px;
  color: #fff;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s; }
  .popup .popup-close {
    position: absolute;
    top: 20px;
    right: 20px;
    cursor: pointer; }
  .popup__input, .popup__textarea {
    margin-bottom: 30px; }
  .popup .button {
    width: 100%;
    margin-bottom: 30px; }

.popup-overlay {
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.5);
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s; }

.popup.active,
.popup-overlay.active {
  opacity: 1;
  visibility: visible; }

